import { customElement, property, state } from 'lit/decorators.js';
import { classMap } from 'lit/directives/class-map.js';
import { html, TemplateResult } from 'lit';
import { unsafeHTML } from 'lit/directives/unsafe-html.js';

import { Template } from '../base';
import '../shared/zwc-template';

type LinkData = {
  href: string;
  target: string;
};
@customElement('zwc-card')
export class ZwcCard extends Template {
  @property({ type: String, attribute: 'background-class' })
  cardBackground = '';

  @property({ type: Boolean, attribute: 'centered' })
  hasCenteredContent = false;

  @property({ type: Boolean, attribute: 'inverted' })
  hasInvertedContent = false;

  @property({ type: Boolean, attribute: 'clickable' })
  hasCardClickable = false;

  @property({ type: Object })
  link: LinkData = { href: '', target: '' };

  /** data will receive HTML string from zwp-template */
  @state()
  data = '';

  render(): TemplateResult {
    const classes = {
      card: true,
      'card--centered': this.hasCenteredContent,
      [`${this.cardBackground}`]: this.cardBackground,
    };

    const contentClasses = {
      'card-content': true,
      inverted: this.hasInvertedContent,
    };

    return html`
      ${this.hasCardClickable
        ? html` <a
            .href=${this.link.href}
            .target=${this.link.target}
            class=${classMap(classes)}
          >
            <div class=${classMap(contentClasses)}>
              ${unsafeHTML(this.data)}
            </div>
          </a>`
        : html`<div class=${classMap(classes)}>
            <div class=${classMap(contentClasses)}>
              ${unsafeHTML(this.data)}
            </div>
          </div>`}
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-card': ZwcCard;
  }
}
